.hero {
    background: url("../img/photos/photo4-tiny.jpg") no-repeat center center;
    background-size: cover;
    height: 60vh;
    margin-bottom: 1rem;
    padding: 1.5rem 3rem;
}
.info {
    float: right;
    margin-top: 47vh;
}

.attribution {
    color: $light-grey;
    font-size: 0.95rem;
    float: left;
    text-align: right;
}

.attribution b {
    color: $dark-grey;
    display: block;
    font-weight: 500;
}
.avatar {
    border-radius: 50%;
    float: right;
    height: 50px;
    margin-left: 1rem;
    width: 50px;
}